.js-start {
  width: 220px;
  height: 40px;
  text-transform: uppercase;
  letter-spacing: 6px;
  cursor: pointer;
  margin: 0 auto;
  display: block;
}

.js-container {
  width: 492px;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 100px auto 0;
  display: flex;
}

.item {
  height: 150px;
  width: 150px;
  text-align: center;
  border-radius: 4px;
  outline: 6px solid gray;
  margin: 0;
  padding: 0;
  font-size: 125px;
  transition: all .3s ease-in-out;
}

.slide-in-blurred-top {
  animation: slide-in-blurred-top .9s cubic-bezier(.23, 1, .32, 1) both;
}

@keyframes slide-in-blurred-top {
  0% {
    transform-origin: 50% 0;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0;
    transform: translateY(-1000px)scaleY(2.5)scaleX(.2);
  }

  100% {
    transform-origin: 50%;
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
    transform: translateY(0)scaleY(1)scaleX(1);
  }
}

